import Vue from 'vue'
import Vuex from 'vuex'
import AuthService from './services/AuthService'
// import MapService from './services/MapService'
import ChefService from './services/ChefService'
import MenuService from "./services/MenuService";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    showMenu: false,
    logeado: false,
    token: '',
    chefs: [],
    loader: false,
    idChef: ''
  },
  mutations: {
  },
  actions: {
    setLoader ({ state }, flag) {
      state.loader = flag
    },
    setID ({ state }, id) {
      state.idChef = id
    },
    login (context, data) {
      return AuthService.login(data.email, data.password)
    },
    changeMenu (flag) {
      this.state.showMenu = flag
    },
    getMe () {
      return ChefService.getMe()
    },
    setToken (context, token) {
      context.state.logeado = true
      AuthService.setToken(token)
    },
    saveMe ({ state }, data) {
      return ChefService.addChef(data)
    },
    getMenu ({ state }) {
      return MenuService.getMenu(state.idChef)
    },
    createMenu ({ state }, data) {
      return MenuService.createMenu(data)
    },
    myOrdersToday ({state}) {
      return MenuService.myOrdersToday()
    },
    setOrderReady ({state}, id) {
      return MenuService.setOrderReady(id)
    },
    setOrderDeliver ({state}, id) {
      return MenuService.setOrderDeliver(id)
    }
  },
  getters: {
  }
})
