import Service from './Service'
let base = 'https://api.donalolafood.com/api/'
class MenuService extends Service {
  getDishes (id) {
    return this.request({
      url: base + 'food-menu/local/' + id,
      method: 'GET',
      body: {
        'idLocal': id
      }
    })
  }
  putOrder (data) {
    return this.request({
      url: base + 'orders/add',
      method: 'POST',
      body: data
    })
  }
  getMyOrders () {
    return this.request({
      url: base + 'orders/user',
      method: 'GET'
    })
  }
  getMenu (id) {
    return this.request({
      url: base + 'food-menu/today',
      method: 'GET'
    })
  }
  createMenu (data) {
    return this.request({
      url: base + 'food-menu/add',
      method: 'POST',
      body: data
    })
  }
  myOrdersToday () {
    return this.request({
      url: base + 'orders/me/today',
      method: 'GET'
    })
  }
  setOrderReady (orderId) {
    return this.request({
      url: base + `orders/${orderId}/ready`,
      method: 'POST'
    })
  }
  setOrderDeliver (orderId) {
    return this.request({
      url: base + `orders/${orderId}/deliver`,
      method: 'POST'
    })
  }
}

export default new MenuService()
