<template>
  <v-container fluid class="profile lola">
    <sidemenu v-show="flagCompleteData"></sidemenu>
    <h2 class="title">Mi Perfil</h2>
    <div class="profile-ctn main-ctn">
      <template>
        <v-form v-model="valid">
          <div class="ctn-avatar">
            <v-avatar
              size="20vh"
              color="grey lighten-4"
            >
              <img :src="profile.image ? profile.image : defaultAvatar" alt="avatar">
            </v-avatar>
            <v-btn class="edit-photo" color="primary" @click="photoCharge" fab small dark>
              <v-icon>edit</v-icon>
            </v-btn>
            <input id="uploadImage" multiple accept="images/*" type="file" @change="uploadImage">
          </div>
          <v-text-field
            v-model="profile.name"
            label="Nombre"
            required
          ></v-text-field>
          <v-text-field
            v-model="profile.dni"
            label="DNI"
            required
            numeric
          ></v-text-field>
          <v-text-field
            v-model="profile.location.address"
            label="Dirección"
            required
          ></v-text-field>
          <div class="ctn-map">
            <label for="">Indique su ubicación</label>
            <div id="map">
            </div>
            <div class="chef-marker blue">
              <img class="chef-center" src="../assets/img/chef.png" alt="">
            </div>
          </div>
          <v-text-field
            v-model="profile.phone"
            label="Celular"
            required
          ></v-text-field>
          <div class="ctn-hour">

          </div>
          <label for="">Horario</label>
          <br/>
          <v-layout row wrap>
            <v-flex xs6 sm6>
              <v-dialog
                ref="dialogOpen"
                v-model="modal2"
                :return-value.sync="profile.openingSchedule"
                persistent
                lazy
                full-width
                width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="profile.openingSchedule"
                  label="Desde"
                  prepend-icon="access_time"
                  readonly
                ></v-text-field>
                <v-time-picker
                  v-if="modal2"
                  v-model="profile.openingSchedule"
                  full-width
                >
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="modal2 = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="$refs.dialogOpen.save(profile.openingSchedule)">OK</v-btn>
                </v-time-picker>
              </v-dialog>
            </v-flex>

            <v-flex xs6 sm6>
              <v-dialog
                ref="dialogClose"
                v-model="modal3"
                :return-value.sync="profile.closingSchedule"
                persistent
                lazy
                full-width
                width="290px"
              >
                <v-text-field
                  slot="activator"
                  v-model="profile.closingSchedule"
                  label="Hasta"
                  prepend-icon="access_time"
                  readonly
                ></v-text-field>
                <v-time-picker
                  v-if="modal3"
                  v-model="profile.closingSchedule"
                  full-width
                >
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="modal2 = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="$refs.dialogClose.save(profile.closingSchedule)">OK</v-btn>
                </v-time-picker>
              </v-dialog>
            </v-flex>
          </v-layout>
          <div class="ctn text-center">
            <v-btn
              color="blue"
              class="white--text"
              @click="editChef">Guardar</v-btn>
          </div>
        </v-form>
      </template>
    </div>
    <welcome :flag-show="flagWelcome" @close="flagWelcome = false"></welcome>
  </v-container>
</template>

<script>
import sidemenu from '../components/Sidemenu'
import { mapActions } from 'vuex'
import Welcome from '../components/Welcome'

export default {
  name: 'Profile',
  data () {
    return {
      valid: false,
      profile: {
        dni: '',
        image: '',
        phone: '',
        openingSchedule: '00:00:00',
        closingSchedule: '00:00:00',
        location: {
          address: '',
          district: 'Lima',
          latitude: -12.082988,
          longitude: -12.082988
        }
      },
      flagWelcome: false,
      flagCompleteData: false,
      map: {},
      currentLocation: {},
      center: {},
      modal2: false,
      modal3: false,
      defaultAvatar: `data:image/png;base64,`
    }
  },
  mounted () {
    this.getMyData()
    this.getLocation()
  },
  methods: {
    ...mapActions([
      'getMe',
      'saveMe',
      'setLoader',
      'setID'
    ]),
    getLocation () {
      navigator.geolocation.getCurrentPosition(
        this.showPosition,
        this.errorCallback,
        {
          maximumAge: 3000,
          timeout: 5000,
          enableHighAccuracy: true
        })
    },
    showPosition (position) {
      this.currentLocation.latitude = parseFloat(position.coords.latitude)
      this.currentLocation.longitude = parseFloat(position.coords.longitude)
      this.map = new google.maps.Map(document.getElementById('map'), {
        center: {
          lat: this.currentLocation.latitude,
          lng: this.currentLocation.longitude
        },
        zoom: 14,
        mapTypeControl: false,
        streetViewControl: false,
        fullscreenControl: false
      })
      this.profile.location.latitude = this.currentLocation.latitude.toFixed(7)
      this.profile.location.longitude = this.currentLocation.longitude.toFixed(7)
      google.maps.event.addListener(this.map,'center_changed', () => {
          this.profile.location.latitude = this.map.getCenter().lat().toFixed(7)
        this.profile.location.longitude = this.map.getCenter().lng().toFixed(7)
      })
    },
    errorCallback () {
      console.log('error')
    },
    photoCharge () {
      document.querySelector('#uploadImage').click()
    },
    editChef () {
      this.setLoader(true)
      this.saveMe(this.profile).then((data) => {
        this.setLoader(false)
        this.flagCompleteData = true
        this.$router.push('menu')
      }).catch((e) => {
        this.setLoader(false)
        console.log(e)
      })
    },
    getMyData () {
      this.getMe().then((data) => {
        this.profile = data
        this.setID(this.profile.id)
        this.flagCompleteData = true
      }).catch(() => {
        this.flagWelcome = true
      })
    },
    uploadImage (data) {
      let reader = new FileReader()
      reader.readAsDataURL(data.srcElement.files[0])
      reader.onload = () => {
        console.log(reader.result)
        this.profile.image = reader.result
      }
    }
  },
  components: {
    sidemenu,
    Welcome
  }
}
</script>

<style lang="stylus" scoped>
#map
  width 100%
  height 300px
.ctn-map
  position: relative
  .chef-marker
    padding 5px
    border-radius 50% 50% 50% 0
    background #fff
    width 40px
    height 40px
    position absolute
    margin 0 auto
    left 0
    right 0
    top calc(50% - 50px)
    transform rotate(-45deg)
    box-shadow 1px 1px 2px 0px #585858
    background red
    img
      width 100%
      transform rotate(45deg)
.edit-photo
  position: absolute
  bottom: 25px
  left: 80px
  right: 0px
  margin: 0 auto
</style>
<style lang="stylus">
.ctn-avatar
  width 100%
  text-align center
  position relative
  margin-bottom 20px
  img
    width: 100%
#uploadImage
  opacity 0
</style>
