<template>
  <v-container fluid class="menus lola">
    <sidemenu></sidemenu>
    <h2 class="title">Menú de hoy</h2>
    <div class="profile-ctn main-ctn">
      <p v-if="!dishes.items" class="text-center">
        Aun no tiene platos registrados para hoy
      </p>
      <v-flex xs12 class="dish-row" v-for="(item, i) in dishes.items" v-if="dishes.items" v-bind:key="i">
        <v-card color="blue" class="white--text">
          <v-layout row>
            <v-flex xs7>
              <v-card-title primary-title>
                <div>
                  <div class="headline">{{item.name}}</div>
                  <div>{{item.description}}</div>
                  <div>{{item.price | currency('S/')}}</div>
                  <div>Cantidad: {{item.stock}}</div>
                </div>
              </v-card-title>
            </v-flex>
            <v-flex xs5>
              <v-img
                :src="item.image"
                height="125px"
                contain
              ></v-img>
            </v-flex>
          </v-layout>
          <v-divider light></v-divider>
          <!--<v-card-actions class="pa-3 text-center">-->
            <!--<v-btn @click="checkmenu(item)">Ver Menu</v-btn>-->
          <!--</v-card-actions>-->
        </v-card>
      </v-flex>
      <div class="text-center action">
        <v-btn @click="dish = true">
          Agregar plato
        </v-btn>
      </div>
    </div>
    <v-dialog
      v-model="dish"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
      scrollable
    >
      <v-card tile>
        <v-toolbar card dark color="primary">
          <v-btn icon dark @click.native="dish = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Nuevo Plato</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark flat @click.native="dish = false">Cerrar</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-text-field
            v-model="newDish.name"
            label="Nombre"
            required
          ></v-text-field>

          <v-text-field
            v-model="newDish.description"
            label="Descripción"
            required
          ></v-text-field>

          <v-text-field
            v-model="newDish.price"
            label="Precio"
            required
          ></v-text-field>

          <v-text-field
            v-model="newDish.stock"
            label="Cant. Disponible"
            required
          ></v-text-field>

          <div class="ctn-avatar">
            <v-avatar
              size="20vh"
              color="grey lighten-4"
            >
              <img :src="newDish.image ? newDish.image : defaultMenu" alt="avatar">
            </v-avatar>
            <v-btn class="edit-photo" color="primary" @click="photoCharge" fab small dark>
              <v-icon>edit</v-icon>
            </v-btn>
            <input id="uploadImage" type="file" @change="uploadImage">
          </div>
          <div class="ctn text-center">
            <v-btn
              color="blue"
              class="white--text"
              @click="addDishDetail">Agregar</v-btn>
          </div>
        </v-card-text>

        <div style="flex: 1 1 auto;"></div>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import sidemenu from '../components/Sidemenu'
import { mapActions } from 'vuex'
export default {
  name: 'Menu',
  data () {
    return {
      dishes: {
        items: 0
      },
      dish: false,
      newDish: {
        name: '',
        description: '',
        price: '',
        image: '',
        stock: ''
      },
      defaultMenu: `data:image/png;base64,`
    }
  },
  methods: {
    ...mapActions([
      'getMenu',
      'createMenu',
      'setLoader'
    ]),
    loadMenus () {
      this.setLoader(true)
      this.getMenu().then((data) => {
        this.setLoader(false)
        if (data[0]) {
          this.dishes = data[0]
        }
      })
    },
    addDishDetail () {
      let obj = {
        'name': 'Menu del día',
        'items' : [ this.newDish ]
      }
      this.setLoader(true)
      this.createMenu(obj).then((data) => {
        this.setLoader(false)
        this.dish = false
        this.loadMenus()
      })
    },
    photoCharge () {
      document.querySelector('#uploadImage').click()
    },
    uploadImage (data) {
      let reader = new FileReader()
      reader.readAsDataURL(data.srcElement.files[0])
      reader.onload = () => {
        this.newDish.image = reader.result
      }
    }
  },
  mounted () {
    this.loadMenus()
  },
  components: {
    sidemenu
  }
}
</script>

<style lang="stylus" scoped>
.edit-photo
  position: absolute
  bottom: 25px
  left: 80px
  right: 0px
  margin: 0 auto
.action
  margin-top 20px
</style>
