<template>
  <v-container fluid class="orders lola">
    <sidemenu></sidemenu>
    <h2 class="title">Pedidos en curso</h2>
    <div class="orders-ctn main-ctn">
      <v-flex xs12>
        <v-card :color="statusListColors[order.status] + ' lighten-1'" class="card white--text"  v-for="(order, i) in ordersList" v-bind:key="i">
          <v-layout>
            <v-flex xs12>
              <v-card-title primary-title>
                <div>
                  Pedido para: <b>{{order.customerDetails.name}}</b>
                  <br/>
                  Metodo de pago: <b>{{order.paymentMethod === 'CASH' ? 'CASH' : 'TARJETA'}}</b>
                  <br/>
                  Items:
                  <ul class="list-items">
                    <li v-for="(item, i) in order.items" v-bind:key="i">
                      <span>
                        {{item.itemMenuDetails.name}}
                      </span>
                      <b>
                        {{item.itemMenuDetails.price | currency('S/')}}
                      </b>
                    </li>
                  </ul>
                </div>
              </v-card-title>
            </v-flex>
          </v-layout>
          <v-divider light></v-divider>
          <v-card-actions class="pa-3">
            <b>{{statusList[order.status]}}</b>
            <v-spacer></v-spacer>
            <v-btn @click="changeState(order.status, order.id)" v-if="btns[order.status]">{{btns[order.status]}}</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </div>
  </v-container>
</template>
<script>
import sidemenu from '../components/Sidemenu'
import { mapActions } from 'vuex'
export default {
  name: 'Orders',
  data () {
    return {
      ordersList: {},
      statusListColors: {
        'OPEN': 'orange',
        'DELIVERED': 'light-green',
        'READY_TO_DELIVER': 'blue',
        'CANCEL': 'blue-grey'
      },
      statusList: {
        'OPEN': 'En proceso',
        'DELIVERED': 'Entregado',
        'READY_TO_DELIVER': 'Listo para entrega',
        'CANCEL': 'Cancelado'
      },
      btns: {
        'OPEN': 'LISTO',
        'READY_TO_DELIVER': 'ENTREGADO',
        'CANCEL': 'Eliminar'
      }
    }
  },
  mounted () {
    this.showMyOrders()
  },
  methods: {
    ...mapActions([
      'myOrdersToday',
      'setOrderReady',
      'setOrderDeliver',
      'setLoader'
    ]),
    showMyOrders () {
      this.myOrdersToday().then((data) => {
        this.ordersList = data
      })
    },
    changeState (status, id) {
      this.setLoader(true)
      switch (status) {
        case 'OPEN':
          this.setOrderReady(id).then((data) => {
            this.setLoader(false)
            this.showMyOrders()
          })
          break
        case 'READY_TO_DELIVER':
          this.setOrderDeliver(id).then((data) => {
            this.setLoader(false)
            this.showMyOrders()
          })
          break
      }
    }
  },
  components: {
    sidemenu
  }
}
</script>

<style lang="stylus" scoped>
.card
  margin-bottom 10px
</style>
