import Service from './Service'

class ChefService extends Service {
  getMe () {
    return this.request({
      url: 'https://api.donalolafood.com/api/chef/me',
      method: 'GET'
    })
  }

  addChef (query) {
    return this.request({
      url: 'https://api.donalolafood.com/api/chef/save',
      method: 'POST',
      body: query
    })
  }
}

export default new ChefService()
